'use client'

import * as React from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { motion, AnimatePresence } from "framer-motion"
import { ChevronLeft, ChevronRight } from "lucide-react"
import { useLanguage } from "@/lib/i18n"
interface Candidate  {
  id: number
  nom_fr: string
  nom_ar: string
  photo?: string
  totalVoix: number
  pourcentage: number
}

type CandidateHighlightProps = {
  candidats: Candidate[]
}
export function CandidateHighlight({ candidats }: CandidateHighlightProps) {
  const [index, setIndex] = React.useState(0)
  const [direction, setDirection] = React.useState(0)
 const { t } = useLanguage()
  const selected = candidats[index]

  // üîÅ Auto carousel
  React.useEffect(() => {
    if (candidats.length === 0) return

    const interval = setInterval(() => {
      goNext()
    }, 4000)

    return () => clearInterval(interval)
  }, [index, candidats])

  const goNext = () => {
    setDirection(1)
    setIndex((prev) => (prev + 1) % candidats.length)
  }

  const goPrev = () => {
    setDirection(-1)
    setIndex((prev) => (prev - 1 + candidats.length) % candidats.length)
  }

  // üëâ Swipe
  const swipeConfidenceThreshold = 50
  const handleDragEnd = (_: any, info: any) => {
    if (info.offset.x < -swipeConfidenceThreshold) {
      goNext()
    } else if (info.offset.x > swipeConfidenceThreshold) {
      goPrev()
    }
  }

  const getPhotoUrl = (c: Candidate) => {
    if (!c.photo) return "/candidate1.jpg"
    if (c.photo.startsWith("/")) return c.photo
    return `/uploads/${c.photo}`
  }

  if (!selected) return null

  return (
    <div className="mb-8 relative">
      <AnimatePresence mode="wait">
        <motion.div
          key={selected.id}
          drag="x"
          dragConstraints={{ left: 0, right: 0 }}
          dragElastic={0.8}
          onDragEnd={handleDragEnd}
          initial={{ opacity: 0, x: direction === 1 ? 120 : -120 }}
          animate={{ opacity: 1, x: 0 }}
          exit={{ opacity: 0, x: direction === 1 ? -120 : 120 }}
          transition={{ duration: 0.4 }}
        >
          <Card className="bg-white/90 backdrop-blur shadow-lg border-0 relative">
            
            {/* ‚¨ÖÔ∏è Fl√®che gauche */}
            <button
              onClick={goPrev}
              className="absolute left-3 top-1/2 -translate-y-1/2 bg-white/80 hover:bg-white p-2 rounded-full shadow z-10"
            >
              <ChevronLeft className="w-6 h-6" />
            </button>

            {/* ‚û°Ô∏è Fl√®che droite */}
            <button
              onClick={goNext}
              className="absolute right-3 top-1/2 -translate-y-1/2 bg-white/80 hover:bg-white p-2 rounded-full shadow z-10"
            >
              <ChevronRight className="w-6 h-6" />
            </button>

            <CardHeader className="text-center">
              <CardTitle className="text-2xl">{selected.nom_ar}</CardTitle>
              <CardTitle className="text-2xl">{selected.nom_fr}</CardTitle>
            </CardHeader>

            <CardContent className="flex justify-center">
              <motion.img
                src={getPhotoUrl(selected)}
                alt={selected.nom_fr}
                className="w-64 h-64 sm:w-80 sm:h-80 object-cover rounded-xl shadow-md"
              />
              
            </CardContent>

            <CardDescription className="text-center text-lg font-semibold mb-4">
              
             <CardTitle className="text-2xl">{selected.totalVoix}</CardTitle>
             <CardTitle className="text-2xl">{selected.pourcentage.toFixed(2)} %</CardTitle>
              
            </CardDescription>

            {/* üîπ Indicateurs en bas */}
            <div className="flex justify-center gap-2 mt-2 mb-4">
              {candidats.map((_, i) => (
                <span
                  key={i}
                  className={`w-3 h-3 rounded-full ${i === index ? "bg-blue-500" : "bg-gray-300"}`}
                />
              ))}
            </div>

          </Card>
        </motion.div>
      </AnimatePresence>
    </div>
  )
}
