'use client'

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { useLanguage } from "@/lib/i18n"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { BarChart3, MapPin, Vote, Users, Globe, ArrowRight, TrendingUp } from "lucide-react"
import { CandidateHighlight } from "@/components/CandidateHighlight"
import { Badge } from "@/components/ui/badge"
import {
  ResponsiveContainer,
  BarChart as RBarChart,
  Bar,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  PieChart,
  Pie,
  Cell,
} from "recharts"

interface GlobalStats {
  totalWilayas?: number
  totalBureaux: number
  totalCandidats: number
  totalInscrits: number
  totalVotants: number
  totalVoix: number
  totalNuls: number
  totalRejetes: number
  bureauxDepouilles: number
  pourcentageDepouilles: number
  participationRate: number
}

interface CandidatDash {
  id: number
  nom_fr: string
  nom_ar: string
  photo: string 
  logo: string | null
  couleur: string | null
  totalVoix: number
  pourcentage: number
}

export default function Home() {
  const router = useRouter()
  const { t, language, setLanguage } = useLanguage()
  const [globalStats, setGlobalStats] = useState<GlobalStats | null>(null)
  const [candidats, setCandidats] = useState<CandidatDash[]>([])
  const [loading, setLoading] = useState(true)

  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null

  const toggleLanguage = () => setLanguage(language === "fr" ? "ar" : "fr")

  useEffect(() => {
    const load = async () => {
      try {
        const statsRes = await fetch("/api/stats")
        const data = await statsRes.json()
        setGlobalStats(data.globalStats)
        setCandidats(
          (data.candidatsResults || []).sort((a: CandidatDash, b: CandidatDash) => b.totalVoix - a.totalVoix)
        )
      } catch {
        router.push("/login")
      } finally {
        setLoading(false)
      }
    }
    load()
  }, [router])

  const statsCards = [
    {
      title: t("total.voters") || "Total inscrits",
      value: globalStats?.totalInscrits ?? 0,
      description: t("registered.voters") || "√âlecteurs inscrits",
      icon: Users,
    },
    {
      title: t("total.bureaux") || "Bureaux de vote",
      value: globalStats?.totalBureaux ?? 0,
      description: `${globalStats?.bureauxDepouilles ?? 0} ${t("bureauStats.deplouilles") || "d√©pouill√©s"} (${globalStats?.pourcentageDepouilles ?? 0}%)`,
      icon: MapPin,
    },
    {
      title: t("total.candidates") || "Candidats",
      value: globalStats?.totalCandidats ?? 0,
      description: t("candidates.registered") || "Candidats enregistr√©s",
      icon: Vote,
    },
    {
      title: t("participation.rate") || "Taux de participation",
      value: `${globalStats?.participationRate ?? 0}%`,
      description: t("current.rate") || "Actuel",
      icon: TrendingUp,
    },
  ]

  const getRank = (i: number) => (i === 0 ? "ü•á" : i === 1 ? "ü•à" : i === 2 ? "ü•â" : `${i + 1}`)

  return (
    <div className="min-h-screen bg-background">
      {/* HEADER */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16 gap-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-gradient-to-br from-primary to-accent rounded-lg flex items-center justify-center shadow-md">
                <BarChart3 className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-lg font-bold text-primary">{t("app.title")}</h1>
                <p className="text-xs text-muted hidden sm:block">{t("app.subtitle")}</p>
              </div>
            </div>

            <div className="flex items-center gap-2">
              <Button variant="ghost" size="sm" onClick={toggleLanguage} className="gap-2">
                <Globe className="w-4 h-4" />
                <span className="hidden sm:inline">{language === "fr" ? "ÿßŸÑÿπÿ±ÿ®Ÿäÿ©" : "Fran√ßais"}</span>
              </Button>

              <Button
                variant="ghost"
                size="sm"
                className="gap-2"
              //  onClick={() => router.push(token ? "/dashboard" : "/dashboard-guest")}
                onClick={() => router.push('/login')}
              >
                <ArrowRight className="w-4 h-4" />
                {t('auth.signin') || "Acc√©der au dashboard"}
              </Button>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12 space-y-12">
                {/* CANDIDATE HIGHLIGHT */}
        {candidats.length > 0 && <CandidateHighlight candidats={candidats} />}
        {/* STATS CARDS */}
        <section className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {statsCards.map((s, i) => {
            const Icon = s.icon
            return (
              <Card
                key={i}
                className="group border-0 shadow-lg hover:shadow-xl transition-transform duration-300 hover:-translate-y-0.5 bg-gradient-to-b from-white to-slate-50 ring-1 ring-slate-100"
              >
                <CardHeader className="flex justify-between pb-3">
                  <CardTitle className="text-gray-800 text-sm font-medium">{s.title}</CardTitle>
                  <div className="h-10 w-10 rounded-xl bg-blue-50 flex items-center justify-center">
                    <Icon className="h-5 w-5 text-blue-600" />
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold tracking-tight">{s.value}</div>
                  <CardDescription className="mt-1 text-gray-600">{s.description}</CardDescription>
                </CardContent>
              </Card>
            )
          })}
        </section>



        {/* R√âSULTATS & GRAPHIQUES */}
        <section className="grid grid-cols-1 lg:grid-cols-5 gap-8">
          {/* Classement des candidats */}
          <Card className="lg:col-span-2 border-0 shadow-xl bg-white/90 backdrop-blur">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Vote className="h-5 w-5 text-blue-600" />
                {t("results.by.candidate")}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {candidats.length === 0 ? (
                <p className="text-sm text-gray-500">
                  {loading ? t("common.loading") : t("results.noData")}
                </p>
              ) : (
                <ul className="space-y-3">
                  {candidats.map((c, i) => (
                    <li
                      key={c.id}
                      className="flex items-center justify-between rounded-xl border bg-white/80 p-3 hover:bg-white"
                    >
                      <div className="flex items-center gap-3">
                        <span className="text-xl w-8 text-center">{getRank(i)}</span>
                        {c.logo && <img src={c.logo} alt={c.nom_fr} className="w-10 h-10 rounded-full border object-cover" />}
                        <div>
                          <p className="font-semibold leading-tight">{c.nom_fr}</p>
                          <p className="text-sm text-gray-500" dir="rtl">{c.nom_ar}</p>
                        </div>
                      </div>
                      <div className="flex flex-col items-end min-w-40">
                        <Badge variant="outline">{c.totalVoix.toLocaleString()} {t("vote")}</Badge>
                        <div className="w-28 bg-gray-200 h-2 rounded-full mt-2 overflow-hidden">
                          <div className="h-2" style={{ width: `${c.pourcentage.toFixed(1)}%`, backgroundColor: c.couleur || "#3b82f6" }} />
                        </div>
                        <span className="text-xs text-gray-500 mt-1">{c.pourcentage.toFixed(2)}%</span>
                      </div>
                    </li>
                  ))}
                </ul>
              )}
            </CardContent>
          </Card>

          {/* Graphiques */}
          <div className="lg:col-span-3 space-y-6">
            {/* Bar chart */}
            <Card className="border-0 shadow-xl bg-white/90 backdrop-blur">
              <CardHeader>
                <CardTitle>{t("results.by.candidate")}</CardTitle>
              </CardHeader>
              <CardContent>
                <ResponsiveContainer width="100%" height={260}>
                  <RBarChart data={candidats.map(c => ({ nom: c.nom_fr, totalVoix: c.totalVoix }))}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="nom" />
                    <YAxis />
                    <Tooltip />
                    <Bar dataKey="totalVoix">
                      {candidats.map((c, i) => <Cell key={i} fill={c.couleur || "#6366F1"} />)}
                    </Bar>
                  </RBarChart>
                </ResponsiveContainer>
              </CardContent>
            </Card>

            {/* Pie chart */}
            <Card className="border-0 shadow-xl bg-white/90 backdrop-blur">
              <CardHeader>
                <CardTitle>{t("votes.distribution")}</CardTitle>
              </CardHeader>
              <CardContent>
                <ResponsiveContainer width="100%" height={260}>
                  <PieChart>
                    <Pie
                      data={candidats.map(c => ({
                        name: language === "ar" ? c.nom_ar : c.nom_fr,
                        value: parseFloat(c.pourcentage.toFixed(1)),
                      }))}
                      dataKey="value"
                      outerRadius={90}
                      label={({ value }) => `${value}%`}
                    >
                      {candidats.map((c, i) => <Cell key={i} fill={c.couleur || "#6366F1"} />)}
                    </Pie>
                    <Tooltip />
                  </PieChart>
                </ResponsiveContainer>
              </CardContent>
            </Card>
          </div>
        </section>
      </main>
    </div>
  )
}
